.btn::after {
  content: "";
  display: block;
  width: 60px;
  height: 100%;
  background: #F3E500 url("../images/btn-arrow.svg") no-repeat 23px center/contain;
  -webkit-background-size: 20px 20px;
  background-size: 18px 18px;
  position: absolute;
  right: 0;
  top: 0;
  transition: all 0.2s linear;
}
.btn:hover::after {
  background-position: 34px center;
}

