}
.menu-toggler .bars div {
  height: 2px;
  background: #000;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.2s;
  transform-origin: 0 0 0;
}
.menu-toggler .bars div:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
}
.menu-toggler .bars div:last-of-type {
  top: auto;
  bottom: 0;
}

.open-menu .menu-toggler .bars div:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
}
.open-menu .menu-toggler .bars div:first-of-type {
  transform: rotate(45deg);
}
.open-menu .menu-toggler .bars div:last-of-type {
  transform: rotate(-45deg);
}

